import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import Dialog from '@material-ui/core/Dialog'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CloseIcon from '@material-ui/icons/Close'
import Slide from '@material-ui/core/Slide'
import tick from "./images/tick.png";

const styles = {
  appBar: {
    position: 'relative',
  },
  flex: {
    flex: 1,
  }, 
  imgContainer: {
    position: 'relative',
    flex: 1,
    padding: 20,
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  img1: {
    maxWidth: '50%',
  },img2: {
    maxWidth: '50%',
  },
}

function Transition(props) {
  return <Slide direction="up" {...props} />
}

class ImgDialog extends React.Component {
  state = {
    open: false,
  }

  handleClickOpen = () => {
    this.setState({ open: true })
  }

  handleClose = () => {
    this.setState({ open: false })
  }

  render() {
    const { classes } = this.props
    return (
      <Dialog
      className="page"
        fullScreen
        open={!!this.props.img}
        onClose={this.props.onClose}
        TransitionComponent={Transition}
      >
        <AppBar className={classes.appBar} >
          <Toolbar>
            <IconButton
              color="inherit"
              onClick={this.props.onClose}
              aria-label="Close"
            >
              <CloseIcon />
            </IconButton>
            <Typography
              variant="title"
              color="inherit"
              className={classes.flex}
            >
              ++โปรดศึกษาการตั้งค่า ดังรูปอย่างละเอียด++
            </Typography>
          </Toolbar>
        </AppBar>
        <div className={classes.imgContainer}>
          <div style={{display: 'inline', justifyContent:'center', alignItems:'center'}}>
        <p>1.เลือกพิมพ์หรือบันทึกเป็น PDF แล้วปรับให้ไม่มีขอบ</p>
          <img src={this.props.img} alt="Cropped" className={classes.img1} />
          </div>
          <div style={{display: 'inline', justifyContent:'center', alignItems:'center'}}>
          <p>"2. ไม่เลือก" พิมพ์หัวและท้าย</p>
          <img src={tick} className={classes.img2}></img>
          </div>
        </div>
      </Dialog>
    )
  }
}

export default withStyles(styles)(ImgDialog)
